(function() {
  'use strict';

  angular
    .module('raskellclient', [
      'ngSanitize',
      'ui.router',
      'toastr'
      ]);

})();

(function() {
  'use strict';

  angular
    .module('raskellclient')
    .directive('raskellNavbar', raskellNavbar);

  /** @ngInject */
  function raskellNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/navbar/navbar.html',
      scope: {
      }
    };

    return directive;
  }

})();

(function() {
  'use strict';

  angular
    .module('raskellclient')
    .controller('HomeController', HomeController);

  /** @ngInject */
  function HomeController() {
    var vm = this;

    activate();

    function activate() {
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('raskellclient')
    .run(runBlock);

  /** @ngInject */
  function runBlock() {

  }

})();

(function() {
  'use strict';

  angular
    .module('raskellclient')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/home/home.html',
        controller: 'HomeController',
        controllerAs: 'ctrl'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('raskellclient')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('raskellclient')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("raskellclient").run(["$templateCache", function($templateCache) {$templateCache.put("app/components/navbar/navbar.html","<div class=\"navbar\"><ul><li class=\"active\"><a ng-ref=\"#\">Home</a></li><li><a ng-ref=\"#\">About</a></li><li><a ng-ref=\"#\">Contact</a></li></ul></div>");
$templateCache.put("app/home/home.html","<raskell-navbar></raskell-navbar><div class=\"content\">This is Home</div>");
$templateCache.put("app/main/main.html","<acme-navbar></acme-navbar><div class=\"content\">This is JADE Main FILE</div>");
$templateCache.put("app/navbar/navbar.html","<div class=\"navbar\"><ul><li><a ng-ref=\"#\">Home</a></li><li><a ng-ref=\"#\">Tracks Engine</a></li><li><a ng-ref=\"#\">Transformation Engine</a></li><li><a ng-ref=\"#\">Matching Engine</a></li></ul></div>");}]);